.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.Dashboard {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.bg1 {
  background-image: url('../public/bg_final.png'); /* Path to your image */
  background-size: cover;
  background-position: center 80%; /* Centers the image */
  background-repeat: no-repeat;
  height: 92vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  position: relative;
}

.form-container {
  margin-top: -140px;
}

.form-container2 {
  margin-top: -60px;
}

.form {
  background-color: rgba(255, 255, 255, 1);
  display: flex;
  flex-direction: column;
  padding: 40px;
  border-radius: 24px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  max-width: 600px;
  margin: -40px auto 0px;
}

.form label {
  text-align: left;
  margin-bottom: 20px;
  font-family: 'Arial', sans-serif;
  color: #333;
}

.form input {
  border: 1px solid #ddd;
  border-radius: 12px;
  padding: 10px;
  margin-bottom: 5px;
  font-size: 16px;
  transition: border-color 0.3s ease;
  width: 100%;
  box-sizing: border-box;
}

.title1 {
  font-size: 3rem;
  font-weight: bold;
  color: #4B553C;
  text-align: center;
  text-shadow: 2px 2px 8px rgba(0, 0, 0, 0.2);
  position: absolute;
  top: 1%;
  text-shadow: 
    -1px -1px 0 white,
    1px -1px 0 white,
    -1px 1px 0 white,
    1px 1px 0 white,
    2px 2px 8px rgba(0, 0, 0, 0.2);
}

.menu-icon {
  margin-right: 16px;
}

.title {
  flex-grow: 1;
  font-weight: bold;
  letter-spacing: 1px;
}

.nav-links {
  display: flex;
  align-items: center;
}

.nav-button {
  margin: 0 8px;
  font-size: 0.9rem;
  text-transform: none;
}

.nav-button:hover {
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 4px;
}

.pagination {
  display: flex;
  justify-content: center;
  padding: 20px 0;
  list-style: none;
  margin: 0;
}

.pagination .page-item {
  margin: 0 5px;
}

.pagination .page-link {
  padding: 10px 15px;
  border: 1px solid #4B553C;
  border-radius: 5px;
  color: #4B553C;
  text-decoration: none;
  transition: background-color 0.3s, color 0.3s;
}

.pagination .page-link:hover {
  background-color: #4B553C;
  color: white;
}

.pagination .active .page-link {
  background-color: #4B553C;
  color: white;
  border-color: #4B553C;
}

.pagination .page-link:disabled {
  color: #ccc;
  border-color: #ccc;
  pointer-events: none;
}

/* Media Queries for Responsive Design */

/* For Mobile Screens (max-width 600px) */
@media (max-width: 600px) {
  .App-header {
    font-size: calc(12px + 4vmin); /* Increase font size for better readability on small screens */
    padding: 20px; /* Add some padding */
  }

  .form {
    padding: 20px; /* Reduce padding on small screens */
    margin: 0 15px; /* Margin to prevent content from touching screen edges */
  }

  .form input {
    font-size: 14px; /* Smaller text size for input fields */
  }

  .title1 {
    font-size: 2.5rem; /* Slightly smaller title for mobile */
    top: 5%;
  }

  .Dashboard {
    display: block; /* Stack the dashboard items vertically */
    align-items: center;
  }

  .bg1 {
    background-position: center center; /* Adjust background position */
    height: auto; /* Allow the background to scale with screen size */
    min-height: 80vh; /* Set a minimum height for mobile views */
  }

  .pagination .page-link {
    padding: 8px 12px; /* Smaller pagination buttons */
  }

  .pagination {
    flex-direction: column; /* Stack pagination vertically */
    margin: 10px 0;
  }
}

/* For Tablets (max-width 768px) */
@media (max-width: 768px) {
  .App-header {
    font-size: calc(10px + 3vmin);
  }

  .form {
    padding: 30px;
    margin: 0 20px;
  }

  .form input {
    font-size: 15px;
  }

  .title1 {
    font-size: 2.8rem;
    top: 3%;
  }

  .Dashboard {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }

  .bg1 {
    background-position: center center;
    min-height: 85vh;
  }
}

/* For Large Screens */
@media (min-width: 1024px) {
  .App-header {
    font-size: calc(10px + 1vmin); /* Smaller font for larger screens */
    padding: 50px;
  }

  .Dashboard {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  .form {
    max-width: 800px; /* Increase max-width on larger screens */
  }
}
